'use client';

import { useEffect, useMemo, useState } from 'react';
import ProjectDashboard from '../components/ProjectDashboard';
import TaskPanel from '../components/TaskPanel';
import TeamOverview from '../components/TeamOverview';
import StudioInsights from '../components/StudioInsights';
import useWorkspace from '../hooks/useWorkspace';

export default function HomePage() {
  const [selectedProjectId, setSelectedProjectId] = useState(null);
  const {
    projects,
    teamMembers,
    loading,
    projectSyncing,
    handleProjectCreate: createProjectApi,
    handleProjectDelete: deleteProjectApi,
    handleProgressUpdate,
    handleProjectHydrate,
    refreshTeam,
    handleTeamCreate: createTeamMember,
  } = useWorkspace();

  useEffect(() => {
    if (!projects.length) {
      if (selectedProjectId !== null) {
        setSelectedProjectId(null);
      }
      return;
    }
    const exists = projects.some((project) => project._id === selectedProjectId);
    if (!exists) {
      setSelectedProjectId(projects[0]._id);
    }
  }, [projects, selectedProjectId]);

  const handleProjectCreate = async (payload) => {
    const project = await createProjectApi(payload);
    setSelectedProjectId(project?._id ?? null);
  };

  const handleProjectDelete = async (projectId) => {
    await deleteProjectApi(projectId);
  };

  const handleTeamCreate = createTeamMember;

  const selectedProject = useMemo(() => {
    if (!projects.length) {
      return null;
    }
    return projects.find((project) => project._id === selectedProjectId) ?? projects[0];
  }, [projects, selectedProjectId]);

  const studioSnapshot = useMemo(() => {
    const totalProjects = projects.length;
    const completed = projects.filter((project) => project.status === 'Completed').length;
    const active = totalProjects - completed;
    const tasks = projects.reduce((acc, project) => acc + (project.taskSummary?.total || 0), 0);
    const now = Date.now();
    const dueSoonBoundary = now + 1000 * 60 * 60 * 24 * 7;
    const dueSoon = projects.filter((project) => {
      if (!project?.dueDate || project.status === 'Completed') return false;
      const time = new Date(project.dueDate).getTime();
      return time >= now && time <= dueSoonBoundary;
    }).length;
    const studioCount = new Set(projects.map((project) => project.studio).filter(Boolean)).size;

    return {
      totalProjects,
      active,
      completed,
      tasks,
      dueSoon,
      studios: studioCount,
    };
  }, [projects]);

  const overviewStats = useMemo(
    () => [
      { label: 'Projects', value: studioSnapshot.totalProjects, caption: 'Total in studio' },
      { label: 'Active', value: studioSnapshot.active, caption: 'Currently in delivery' },
      { label: 'Completed', value: studioSnapshot.completed, caption: 'Shipped this quarter' },
      { label: 'Due this week', value: studioSnapshot.dueSoon, caption: 'Need follow-up' },
    ],
    [studioSnapshot],
  );

  const handleProjectCreate = async (payload) => {
    const project = await api.createProject(payload);
    const normalized = normalizeProject(project);
    setProjects((prev) => [normalized, ...prev]);
    setSelectedProjectId(normalized._id);
  };

  const handleProjectDelete = async (projectId) => {
    const project = projects.find((item) => item._id === projectId);
    if (!project) return;
    if (!window.confirm(`Delete ${project.name}? Tasks for this project will also be removed.`)) {
      return;
    }
    await api.deleteProject(projectId);
    const wasSelected = selectedProjectId === projectId;
    setProjects((prev) => {
      const next = prev.filter((item) => item._id !== projectId);
      if (wasSelected) {
        setSelectedProjectId(next[0]?._id ?? null);
      }
      return next;
    });
  };

  const handleProgressUpdate = async (projectId, progressValue) => {
    setProjectSyncing((prev) => {
      const next = new Set(prev);
      next.add(projectId);
      return next;
    });
    try {
      const updated = await api.updateProjectProgress(projectId, progressValue);
      setProjects((prev) =>
        prev.map((project) =>
          project._id === projectId ? normalizeProject({ ...project, ...updated }) : project,
        ),
      );
    } finally {
      setProjectSyncing((prev) => {
        const next = new Set(prev);
        next.delete(projectId);
        return next;
      });
    }
  };

  const handleProjectHydrate = (updatedProject) => {
    if (!updatedProject) return;
    setProjects((prev) =>
      prev.map((project) => (project._id === updatedProject._id ? normalizeProject(updatedProject) : project)),
    );
  };

  const refreshTeam = async () => {
    const list = await api.getTeamMembers();
    setTeamMembers(list);
  };

  const handleTeamCreate = async (payload) => {
    await api.createTeamMember(payload);
    await refreshTeam();
  };

  if (loading) {
    return (
      <main className="flex min-h-screen items-center justify-center text-slate-400">
        <div className="space-y-2 text-center">
          <p className="text-sm uppercase tracking-[0.3em]">Loading studio</p>
          <div className="h-1 w-40 overflow-hidden rounded-full bg-white/10">
            <div className="h-full w-1/2 animate-pulse rounded-full bg-sky-400" />
          </div>
        </div>
      </main>
    );
  }

  return (
    <main className="space-y-8">
      <header className="surface-card space-y-4">
        <div>
          <p className="text-xs font-semibold uppercase tracking-[0.35em] text-slate-400">Dashboard</p>
          <h1 className="mt-2 text-3xl font-semibold text-slate-900">Arch studio overview</h1>
          <p className="text-sm text-slate-500">
            Manage and track all your firmâ€™s projects, tasks, and teams in one calm workspace.
          </p>
        </div>
        <div className="grid gap-3 sm:grid-cols-2 lg:grid-cols-4">
          {overviewStats.map((stat) => (
            <article key={stat.label} className="rounded-2xl border border-slate-200 bg-slate-50 px-4 py-3">
              <p className="text-[11px] uppercase tracking-[0.35em] text-slate-400">{stat.label}</p>
              <p className="mt-2 text-2xl font-semibold text-slate-900">{stat.value ?? 0}</p>
              <p className="text-xs text-slate-500">{stat.caption}</p>
            </article>
          ))}
        </div>
      </header>

      <ProjectDashboard
        projects={projects}
        selectedProjectId={selectedProjectId}
        onSelect={setSelectedProjectId}
        onCreate={handleProjectCreate}
        onDelete={handleProjectDelete}
        onProgressUpdate={handleProgressUpdate}
        busyProjectIds={projectSyncing}
      />

      <section className="grid gap-6 xl:grid-cols-[1.1fr_0.9fr]">
        <TaskPanel
          project={selectedProject}
          teamMembers={teamMembers}
          onProjectUpdate={handleProjectHydrate}
          onTeamRefresh={refreshTeam}
        />
        <StudioInsights projects={projects} teamMembers={teamMembers} />
      </section>

      <TeamOverview teamMembers={teamMembers} onCreate={handleTeamCreate} />
    </main>
  );
}
